.centerCartBlock {
	padding-bottom: 50px; }
.centerCartContainer {
	@include row-flex();
	justify-content: space-between;
	align-items: flex-start;
	@include lg(justify-content, center); }
.centerCart {
	@include col();
	@include size(5.5);
	@include size-lg(6);
	@include size-md(10);
	@include size-sm(12);
	@include size-xs(12);
	margin-top: 60px;
	&__img {
		height: 300px;
		border-radius: 10px;
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__title {
		font-family: $blogger;
		font-weight: 700;
		font-size: $big-font-size;
		line-height: 33px;
		text-transform: uppercase;
		color: $purple;
		margin: 30px 0 10px 0;
		@include md-block {
			font-size: 24px;
			text-align: center; } }
	p, ul {
		font-family: $roboto;
		font-weight: normal;
		font-size: $base-font-size;
		line-height: 22px;
		color: $grey; } }
