@import "modules/blocks/color-circle.sass";
.headerContainer {
	position: relative;
	padding-left: 30px;
	padding-right: 30px;
	z-index: 100;
	@include xl-block {
		padding-top: 17px;
		padding-bottom: 17px; }
	@include lg-block {
		background: $violet;
		border-bottom: 1px solid $white;
		position: fixed;
		width: 100%; }
	@include xs-block {
		padding-left: 10px;
		padding-right: 10px; }
	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__burger {
		display: none;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		height: 40px;
		width: 50px;
		@include xl(display, flex);
		@include md(align-self, flex-start);
		.line {
			display: block;
			position: relative;
			width: 40px;
			height: 4px;
			background: $violet;
			border-radius: 100px;
			@include lg(background, $white);
			&:before,&:after {
				position: absolute;
				content: '';
				width: 40px;
				height: 4px;
				background: $violet;
				border-radius: 100px;
				@include lg(background, $white); }
			&:before {
				bottom: 0;
				left: 0;
				margin-bottom: 14px; }
			&:after {
				top: 0;
				left: 0;
				margin-top: 14px; } } }
	&__brand {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 5;
		@include md-block {
			flex-wrap: wrap; }
		&-logo {
			margin-right: 25px;
			height: 35px;
			text-align: center;
			@include md-block {
				margin-right: 25px;
				margin-left: 25px; }
			@include xs-block {
				margin-right: 10px;
				margin-left: 10px; }
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; } }
		&-phone {
			display: flex;
			align-items: center;
			text-align: center;
			@include sm-block {
				margin-top: 20px;
				flex-grow: 0; }
			img {
				height: 30px;
				width: 30px;
				margin-top: 5px;
				margin-right: 10px; }
			span {
				font-family: $blogger;
				font-weight: 700;
				font-size: $big-font-size;
				color: $white;
				@include sm(font-size, 24px);
				@include xs(font-size, 18px); } } } }
