.contactsBlock {
	padding-top: 50px;
	padding-bottom: 50px;
	&__title {
		font-family: $blogger;
		font-size: $big-font-size;
		font-weight: 700;
		line-height: 33px;
		text-transform: uppercase;
		text-align: center;
		color: $purple;
		@include md(font-size, 24px); } }
.contactsAddressContainer {
	display: flex;
	justify-content: space-around;
	align-items: center;
	@include sm-block {
		flex-direction: column;
		justify-content: center; } }
.contactsAddress {
	margin-top: 50px;
	@include sm(padding, 0 15px);
	&_purple {
		color: $purple; }
	&_orange {
		color: $orange; }
	span {
		display: block;
		font-family: $blogger;
		font-size: $big-font-size;
		font-weight: 700;
		line-height: 33px;
		text-transform: uppercase;
		font-style: normal;
		text-align: center;
		@include md(font-size, 24px); } }
