.aboutProgrammBlock {
	padding-top: 50px;
	position: relative;
	z-index: 15;
	@include lg(padding-top, 75px);
	@include sm(padding-top, 125px);
	&_padding {
		padding-bottom: 100px;
		@include md(padding-bottom, 0); } }
.aboutProgrammContainer {
	@include row-flex();
	justify-content: flex-end;
	align-items: flex-start; }
/* Images Programm School */
.aboutProgrammImageSchool {
	position: absolute;
	top: 300px;
	left: 100px;
	z-index: 15;
	@include lg(display, none);
	&__text {
		position: absolute;
		top: -60px;
		right: -210px;
		span {
			display: block;
			font-family: $blogger; }
		&-title {
			font-size: 50px;
			line-height: 55px;
			font-weight: 700;
			text-transform: uppercase;
			color: $white;
			text-align: right; }
		&-subtitle {
			font-size: 36px;
			font-weight: normal;
			color: $white;
			text-align: right;
			line-height: 40px; } }
	&__img {
		height: 346px;
		img {
			max-width: 100%;
			height: 100%;
			object-fit: contain; } }
	&__star {
		position: absolute;
		z-index: 15;
		img {
			width: 100%;
			height: auto; }
		&_one {
			top: -130px;
			left: 130px; }
		&_two {
			top: -80px;
			left: 20px; }
		&_three {
			top: -125px;
			right: -220px; }
		&_four {
			top: -150px;
			right: -80px; }
		&_five {
			bottom: 135px;
			right: -70px; }
		&_six {
			top: -155px;
			right: -135px; } }
	&__circle {
		border-radius: 50%;
		border: 2px solid $white;
		position: absolute;
		background: $white;
		&_one {
			width: 18px;
			height: 18px;
			top: 0px;
			right: 100px; }
		&_two {
			width: 22px;
			height: 22px;
			top: 40px;
			right: -250px; }
		&_three {
			width: 33px;
			height: 33px;
			bottom: 200px;
			right: -160px; }
		&_four {
			width: 21px;
			height: 21px;
			top: -120px;
			right: 20px; }
		&_five {
			width: 10px;
			height: 10px;
			top: -160px;
			right: 100px; }
		&_six {
			width: 18px;
			height: 18px;
			bottom: 200px;
			right: -30px; }
		&_seven {
			width: 14px;
			height: 14px;
			top: -140px;
			left: 80px; } } }
