/* Backgraund Page Programm */
.aboutProgrammBg__star {
	position: absolute;
	z-index: -1;
	@include lg(display, none);
	img {
		width: 100%;
		height: auto;
		object-fit: contain; }
	&_one {
		top: 50%;
		left: -50px; }
	&_two {
		top: 15%;
		right: -10%; }
	&_three {
		bottom: 6%;
		right: -10%;
		transform: rotate(30deg); }
	&_four {
		bottom: -38%;
		left: 35%; } }
.aboutProgrammBg__circle {
	position: absolute;
	border-radius: 50%;
	background: transparent;
	border: 2px solid $purple;
	z-index: -1;
	@include lg(display, none);
	&_one {
		width: 132px;
		height: 132px;
		top: 56%;
		left: 38%; }
	&_two {
		width: 60px;
		height: 60px;
		bottom: 17%;
		left: 38%; }
	&_three {
		width: 92px;
		height: 92px;
		bottom: -15%;
		left: -1%; }
	&_four {
		width: 241px;
		height: 241px;
		bottom: -37%;
		right: -5%; } }
/* Text Page Programm */
.aboutProgrammText {
	@include col();
	@include size(5);
	@include size-lg(12);
	&__title {
		font-family: $blogger;
		font-weight: 700;
		font-size: $big-font-size;
		line-height: 33px;
		text-transform: uppercase;
		color: $purple;
		margin: 30px 0 0 0;
		@include md(font-size, 24px); }
	&__descr {
		font-family: $roboto;
		font-weight: normal;
		font-size: $base-font-size;
		color: $grey;
		line-height: 22px; }
	&__img {
		height: 300px;
		border-radius: 10px;
		width: 100%;
		overflow: hidden;
		margin-top: 50px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
